import setActionSagaHandler from './internal/setActionSagaHandler'
import isGenerator from './internal/isGenerator'
import Domain from '../Domain'
import Saga from '../types/Saga'

export default function(sagaGeneratorFunction: Saga) {
  return (target: Domain<any>, key: string, descriptor: PropertyDescriptor) => {
    const actionType = descriptor.value.actionType;

    if (!actionType) {
      throw new Error(
        `The method ${key} must be defined as an action with the @action decorator before you can handle it. ` +
        'Ensure that the @action decorator is after the @saga decorator.'
      );
    }

    setActionSagaHandler(target, actionType, sagaGeneratorFunction);

    return descriptor;
  }
}



