import setActionReducerHandler from './internal/setActionReducerHandler'
import SmartReducer from '../types/SmartReducer'
import Domain from '../Domain'

export default function(reducerFunction: SmartReducer<any, any>) {
  return (target: Domain<any>, key: string, descriptor: any) => {
    const actionType = descriptor.value.actionType;

    if (!actionType) {
      throw new Error(
        `The method ${key} must be defined as an action with the @action decorator before you can handle it. ` +
        'Ensure that the @action decorator is after the @reducer decorator.'
      );
    }

    setActionReducerHandler(target, actionType, reducerFunction);

    return descriptor;
  }
}



